/**
 * Image Pairs - Match contextual gaming images (champions, items, etc.)
 * Mobile-first design with touch optimization
 * Uses emojis as fallback for MVP (can be replaced with real images later)
 */

import { useState, useEffect, useCallback, useRef } from 'react'
import { MemoryExerciseConfig } from '@/types/memoryExercise'
import { Button } from '@/components/ui'

interface ImagePairsProps {
  config: MemoryExerciseConfig
  onComplete: (stats: GameStats) => void
  onProgress?: (stats: GameStats) => void
}

interface GameStats {
  total_moves: number
  correct_moves: number
  incorrect_moves: number
  time_elapsed_ms: number
  completed: boolean
}

interface PairCard {
  id: number
  pairId: number
  type: 'question' | 'answer'
  content: string
  label: string
  isFlipped: boolean
  isMatched: boolean
}

// MOBA-themed pairs (champion abilities, items, stats, etc.)
const MOBA_PAIRS = [
  { question: '', answer: '', label: 'Arme' },
  { question: '', answer: '', label: 'Armure' },
  { question: '', answer: '', label: 'Dgts Magiques' },
  { question: '', answer: '', label: 'Vitesse' },
  { question: '', answer: '', label: 'Sant' },
  { question: '', answer: '', label: 'Prcision' },
  { question: '', answer: '', label: 'Gold' },
  { question: '', answer: '', label: 'Niveau' },
  { question: '', answer: '', label: 'Ralentissement' },
  { question: '', answer: '', label: 'tourdissement' },
  { question: '', answer: '', label: 'Mana' },
  { question: '', answer: '', label: 'Vision' },
  { question: '', answer: '', label: 'Ward' },
  { question: '', answer: '', label: 'Objectif' },
  { question: '', answer: '', label: 'Tour' },
  { question: '', answer: '', label: 'Comptence' },
  { question: '', answer: '', label: 'Zone' },
  { question: '', answer: '', label: 'Force' },
  { question: '', answer: '', label: 'Mobilit' },
  { question: '', answer: '', label: 'Passif' },
]

export default function ImagePairs({
  config,
  onComplete,
  onProgress,
}: ImagePairsProps) {
  const rows = config.grid_rows || 4
  const cols = config.grid_cols || 4
  const totalPairs = (rows * cols) / 2

  const [cards, setCards] = useState<PairCard[]>([])
  const [selectedCards, setSelectedCards] = useState<number[]>([])
  const [matchedPairs, setMatchedPairs] = useState<number>(0)
  const [moves, setMoves] = useState<number>(0)
  const [correctMoves, setCorrectMoves] = useState<number>(0)
  const [incorrectMoves, setIncorrectMoves] = useState<number>(0)
  const [startTime, setStartTime] = useState<number>(0)
  const [timeElapsed, setTimeElapsed] = useState<number>(0)
  const [gameStarted, setGameStarted] = useState<boolean>(false)
  const [gameCompleted, setGameCompleted] = useState<boolean>(false)
  const [canSelect, setCanSelect] = useState<boolean>(true)
  const [hintPairId, setHintPairId] = useState<number | null>(null)

  const timerRef = useRef<NodeJS.Timeout | null>(null)

  useEffect(() => {
    initializeGame()
    return () => {
      if (timerRef.current) clearInterval(timerRef.current)
    }
  }, [config])

  useEffect(() => {
    if (gameStarted && !gameCompleted) {
      timerRef.current = setInterval(() => {
        const elapsed = Date.now() - startTime
        setTimeElapsed(elapsed)

        if (onProgress && elapsed % 5000 < 100) {
          onProgress(getStats(elapsed, false))
        }
      }, 100)

      return () => {
        if (timerRef.current) clearInterval(timerRef.current)
      }
    }
  }, [gameStarted, gameCompleted, startTime])

  useEffect(() => {
    if (matchedPairs === totalPairs && totalPairs > 0 && gameStarted) {
      completeGame()
    }
  }, [matchedPairs, totalPairs])

  const initializeGame = () => {
    const selectedPairs = MOBA_PAIRS.slice(0, totalPairs)
    const allCards: PairCard[] = []

    selectedPairs.forEach((pair, index) => {
      allCards.push({
        id: index * 2,
        pairId: index,
        type: 'question',
        content: pair.question,
        label: pair.label,
        isFlipped: false,
        isMatched: false,
      })
      allCards.push({
        id: index * 2 + 1,
        pairId: index,
        type: 'answer',
        content: pair.answer,
        label: pair.label,
        isFlipped: false,
        isMatched: false,
      })
    })

    // Shuffle
    const shuffled = allCards
      .sort(() => Math.random() - 0.5)
      .map((card, index) => ({ ...card, id: index }))

    setCards(shuffled)
    setSelectedCards([])
    setMatchedPairs(0)
    setMoves(0)
    setCorrectMoves(0)
    setIncorrectMoves(0)
    setTimeElapsed(0)
    setGameStarted(false)
    setGameCompleted(false)
    setCanSelect(true)
    setHintPairId(null)
  }

  const handleCardClick = (cardId: number) => {
    if (!canSelect) return
    if (gameCompleted) return
    if (selectedCards.includes(cardId)) return
    if (cards[cardId].isMatched) return
    if (selectedCards.length >= 2) return

    if (!gameStarted) {
      setGameStarted(true)
      setStartTime(Date.now())
    }

    const newSelected = [...selectedCards, cardId]
    setSelectedCards(newSelected)

    setCards((prev) =>
      prev.map((card) =>
        card.id === cardId ? { ...card, isFlipped: true } : card
      )
    )

    if (newSelected.length === 2) {
      setCanSelect(false)
      setMoves((prev) => prev + 1)

      const [firstId, secondId] = newSelected
      const firstCard = cards[firstId]
      const secondCard = cards[secondId]

      if (firstCard.pairId === secondCard.pairId) {
        // Match!
        setCorrectMoves((prev) => prev + 1)
        setHintPairId(firstCard.pairId)

        setTimeout(() => {
          setCards((prev) =>
            prev.map((card) =>
              card.id === firstId || card.id === secondId
                ? { ...card, isMatched: true }
                : card
            )
          )
          setMatchedPairs((prev) => prev + 1)
          setSelectedCards([])
          setCanSelect(true)
          setHintPairId(null)
        }, 1000)
      } else {
        // No match
        setIncorrectMoves((prev) => prev + 1)

        setTimeout(() => {
          setCards((prev) =>
            prev.map((card) =>
              card.id === firstId || card.id === secondId
                ? { ...card, isFlipped: false }
                : card
            )
          )
          setSelectedCards([])
          setCanSelect(true)
        }, 1200)
      }
    }
  }

  const getStats = (elapsed: number, completed: boolean): GameStats => ({
    total_moves: moves,
    correct_moves: correctMoves,
    incorrect_moves: incorrectMoves,
    time_elapsed_ms: elapsed,
    completed,
  })

  const completeGame = () => {
    if (gameCompleted) return

    const elapsed = Date.now() - startTime
    setGameCompleted(true)
    setTimeElapsed(elapsed)

    if (timerRef.current) {
      clearInterval(timerRef.current)
    }

    onComplete(getStats(elapsed, true))
  }

  const formatTime = (ms: number): string => {
    const seconds = Math.floor(ms / 1000)
    const minutes = Math.floor(seconds / 60)
    const remainingSeconds = seconds % 60
    return `${minutes}:${remainingSeconds.toString().padStart(2, '0')}`
  }

  const accuracy = moves > 0 ? ((correctMoves / moves) * 100).toFixed(0) : '0'

  return (
    <div className="h-full flex flex-col">
      {/* Header Stats */}
      <div className="bg-gradient-to-r from-primary-600 to-secondary rounded-lg p-3 sm:p-4 mb-4 shadow-lg">
        <div className="grid grid-cols-4 gap-2 text-center text-white">
          <div>
            <div className="text-xs sm:text-sm opacity-80">Temps</div>
            <div className="text-base sm:text-xl font-bold">
              {formatTime(timeElapsed)}
            </div>
          </div>
          <div>
            <div className="text-xs sm:text-sm opacity-80">Paires</div>
            <div className="text-base sm:text-xl font-bold">
              {matchedPairs}/{totalPairs}
            </div>
          </div>
          <div>
            <div className="text-xs sm:text-sm opacity-80">Essais</div>
            <div className="text-base sm:text-xl font-bold">{moves}</div>
          </div>
          <div>
            <div className="text-xs sm:text-sm opacity-80">Prcision</div>
            <div className="text-base sm:text-xl font-bold">{accuracy}%</div>
          </div>
        </div>
      </div>

      {/* Hint display */}
      {hintPairId !== null && (
        <div className="text-center py-2 bg-green-100 dark:bg-green-900/30 rounded-lg mb-4 animate-pulse">
          <p className="text-sm font-semibold text-green-800 dark:text-green-200">
             {cards.find((c) => c.pairId === hintPairId)?.label}
          </p>
        </div>
      )}

      {/* Game Grid */}
      <div className="flex-1 flex items-center justify-center p-2 sm:p-4">
        <div
          className="grid gap-2 sm:gap-3"
          style={{
            gridTemplateColumns: `repeat(${cols}, 1fr)`,
            gridTemplateRows: `repeat(${rows}, 1fr)`,
            maxWidth: '100%',
            aspectRatio: `${cols} / ${rows}`,
          }}
        >
          {cards.map((card) => (
            <button
              key={card.id}
              onClick={() => handleCardClick(card.id)}
              disabled={!canSelect || card.isMatched || card.isFlipped}
              className={`
                relative aspect-square rounded-lg shadow-md
                transition-all duration-300 transform
                active:scale-95
                ${
                  card.isFlipped || card.isMatched
                    ? 'bg-white dark:bg-gray-100'
                    : 'bg-gradient-to-br from-purple-500 to-pink-600'
                }
                ${card.isMatched ? 'opacity-60 cursor-not-allowed' : ''}
                ${
                  !canSelect && !card.isMatched && !card.isFlipped
                    ? 'cursor-not-allowed'
                    : 'cursor-pointer'
                }
                ${selectedCards.includes(card.id) ? 'ring-4 ring-yellow-400' : ''}
                hover:scale-105
                touch-manipulation
                select-none
              `}
              style={{
                minWidth: '50px',
                minHeight: '50px',
              }}
            >
              {/* Card back */}
              {!card.isFlipped && !card.isMatched && (
                <div className="absolute inset-0 flex flex-col items-center justify-center text-white">
                  <div className="text-2xl sm:text-3xl mb-1"></div>
                  <div className="text-xs opacity-70">?</div>
                </div>
              )}

              {/* Card front */}
              {(card.isFlipped || card.isMatched) && (
                <div
                  className={`
                  absolute inset-0 flex flex-col items-center justify-center
                  ${card.isMatched ? 'animate-bounce-once' : ''}
                `}
                >
                  <div className="text-3xl sm:text-5xl mb-1">{card.content}</div>
                  <div className="text-xs text-gray-600 dark:text-gray-500 text-center px-1">
                    {card.label}
                  </div>
                </div>
              )}
            </button>
          ))}
        </div>
      </div>

      {/* Completion overlay */}
      {gameCompleted && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-white dark:bg-gray-800 rounded-xl p-6 max-w-md w-full text-center shadow-2xl">
            <div className="text-6xl mb-4"></div>
            <h3 className="text-2xl font-bold mb-4 text-gray-900 dark:text-white">
              Excellent !
            </h3>
            <div className="space-y-2 text-gray-700 dark:text-gray-300 mb-6">
              <p>
                <strong>Temps :</strong> {formatTime(timeElapsed)}
              </p>
              <p>
                <strong>Essais :</strong> {moves}
              </p>
              <p>
                <strong>Prcision :</strong> {accuracy}%
              </p>
            </div>
          </div>
        </div>
      )}

      {/* Actions */}
      <div className="mt-4">
        <Button variant="outline" fullWidth onClick={initializeGame}>
           Recommencer
        </Button>
      </div>
    </div>
  )
}
