/**
 * Dashboard Router - Routes to appropriate dashboard based on user role
 */

import { useState, useEffect } from 'react'
import { useAuthStore } from '@/store/authStore'
import { UserRole } from '@/types/user'
import JoueurDashboard from './JoueurDashboard'
import CoachDashboard from './CoachDashboard'
import ManagerDashboard from './ManagerDashboard'
import SuperAdminDashboard from './SuperAdminDashboard'
import MainLayout from '@/components/layout/MainLayout'

export default function DashboardRouter() {
  const { user } = useAuthStore()
  const [useSuperAdminMode, setUseSuperAdminMode] = useState(false)

  // Load super admin mode preference from localStorage
  useEffect(() => {
    if (user?.is_super_admin) {
      const savedMode = localStorage.getItem('superAdminMode')
      setUseSuperAdminMode(savedMode === 'true')
    }
  }, [user])

  // Toggle super admin mode
  const toggleSuperAdminMode = () => {
    const newMode = !useSuperAdminMode
    setUseSuperAdminMode(newMode)
    localStorage.setItem('superAdminMode', String(newMode))
  }

  if (!user) {
    return null
  }

  const renderDashboard = () => {
    // If super admin mode is enabled and user has is_super_admin flag
    if (user.is_super_admin && useSuperAdminMode) {
      return <SuperAdminDashboard onToggleSuperAdminMode={toggleSuperAdminMode} />
    }

    // Regular users get role-based dashboard
    switch (user.role) {
      case UserRole.JOUEUR:
        return <JoueurDashboard />
      case UserRole.COACH:
        return <CoachDashboard onToggleSuperAdminMode={user.is_super_admin ? toggleSuperAdminMode : undefined} />
      case UserRole.MANAGER:
        return <ManagerDashboard />
      case UserRole.SUPER_ADMIN:
        return <SuperAdminDashboard onToggleSuperAdminMode={toggleSuperAdminMode} />
      default:
        return <JoueurDashboard />
    }
  }

  return <MainLayout>{renderDashboard()}</MainLayout>
}
