/**
 * Exercises Page - Catalog of cognitive exercises
 */

import { useState, useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import MainLayout from '@/components/layout/MainLayout'
import { Card, CardHeader, CardBody, Badge, Button } from '@/components/ui'
import exerciseService from '@/services/exerciseService'
import type {
  Exercise,
  ExerciseCategory,
  ExerciseType,
} from '@/types/exercise'
import {
  CATEGORY_LABELS,
  CATEGORY_EMOJIS,
  CATEGORY_COLORS,
  ExerciseCategory as ExerciseCategoryEnum,
  ExerciseType as ExerciseTypeEnum,
} from '@/types/exercise'

export default function ExercisesPage() {
  const navigate = useNavigate()
  const [exercises, setExercises] = useState<Exercise[]>([])
  const [filteredCategory, setFilteredCategory] = useState<ExerciseCategory | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  // Get game route for custom exercises
  const getGameRoute = (exercise: Exercise): string | null => {
    if (exercise.exercise_type !== ExerciseTypeEnum.CUSTOM) return null

    const name = exercise.name.toLowerCase()

    // Memory exercises - check category first
    if (exercise.category === ExerciseCategoryEnum.MEMOIRE) {
      return `/games/memory/${exercise.id}`
    }

    // Other custom games
    if (name.includes('reaction') || name.includes('raction')) {
      return `/games/reaction-time/${exercise.id}`
    }
    if (name.includes('peripheral') || name.includes('priphrique')) {
      return '/games/peripheral-vision'
    }
    if (name.includes('multi-task') || name.includes('multi-tche')) {
      return '/games/multi-task'
    }
    return null
  }

  useEffect(() => {
    loadExercises()
  }, [filteredCategory])

  const loadExercises = async () => {
    setIsLoading(true)
    setError(null)
    try {
      // Debug: Check if token exists
      const token = localStorage.getItem('access_token')
      console.log('[ExercisesPage] Token exists:', !!token, token ? `(${token.substring(0, 20)}...)` : 'NO TOKEN')
      console.log('[ExercisesPage] Loading exercises...', { filteredCategory })

      const data = await exerciseService.getExercises(filteredCategory || undefined)
      console.log('[ExercisesPage] Exercises loaded successfully:', data.length, 'exercises')
      setExercises(data)

      if (data.length === 0) {
        console.warn('[ExercisesPage] No exercises returned from API')
      }
    } catch (err: any) {
      console.error('[ExercisesPage] Failed to load exercises:', err)
      console.error('[ExercisesPage] Error details:', {
        status: err.response?.status,
        statusText: err.response?.statusText,
        data: err.response?.data,
        message: err.message
      })
      const errorMessage = err.response?.data?.detail || err.message || 'Erreur de chargement des exercices'
      setError(errorMessage)
    } finally {
      setIsLoading(false)
    }
  }

  const groupedExercises = exercises.reduce((acc, exercise) => {
    if (!acc[exercise.category]) {
      acc[exercise.category] = []
    }
    acc[exercise.category].push(exercise)
    return acc
  }, {} as Record<ExerciseCategory, Exercise[]>)

  const categories = Object.keys(ExerciseCategoryEnum) as ExerciseCategory[]

  return (
    <MainLayout>
      <div className="space-y-6">
        {/* Header */}
        <div className="bg-gradient-to-r from-primary-600 to-secondary rounded-xl p-8 text-white">
          <h1 className="text-3xl font-bold mb-2">Exercices Cognitifs</h1>
          <p className="text-primary-100">
            Entrane tes comptences essentielles au MOBA : rflexes, vision, mmoire, attention et coordination
          </p>
        </div>

        {/* Category Filters */}
        <Card>
          <CardHeader title="Catgories" subtitle="Filtre par type d'exercice" />
          <CardBody>
            <div className="flex flex-wrap gap-3">
              <Button
                variant={filteredCategory === null ? 'primary' : 'outline'}
                size="sm"
                onClick={() => setFilteredCategory(null)}
              >
                Toutes ({exercises.length})
              </Button>

              {categories.map((category) => {
                const count = groupedExercises[category]?.length || 0
                return (
                  <Button
                    key={category}
                    variant={filteredCategory === category ? 'primary' : 'outline'}
                    size="sm"
                    onClick={() => setFilteredCategory(category)}
                  >
                    {CATEGORY_EMOJIS[category]} {CATEGORY_LABELS[category]} ({count})
                  </Button>
                )
              })}
            </div>
          </CardBody>
        </Card>

        {/* Error Message */}
        {error && (
          <Card>
            <CardBody>
              <div className="text-center py-8">
                <span className="text-6xl mb-4 block"></span>
                <h3 className="text-xl font-semibold text-red-600 dark:text-red-400 mb-2">
                  Erreur de chargement
                </h3>
                <p className="text-gray-600 dark:text-gray-400 mb-4">
                  {error}
                </p>
                <Button variant="primary" onClick={loadExercises}>
                  Ressayer
                </Button>
              </div>
            </CardBody>
          </Card>
        )}

        {/* Loading */}
        {isLoading && !error && (
          <div className="text-center py-12">
            <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600" />
            <p className="mt-4 text-gray-600 dark:text-gray-400">Chargement des exercices...</p>
          </div>
        )}

        {/* Exercises by Category */}
        {!isLoading && !error && Object.entries(groupedExercises)
          .filter(([category]) => !filteredCategory || category === filteredCategory)
          .map(([category, exercises]) => (
          <div key={category} className="space-y-4">
            <div className="flex items-center gap-3">
              <span className="text-4xl">{CATEGORY_EMOJIS[category as ExerciseCategory]}</span>
              <h2 className="text-2xl font-bold text-gray-900 dark:text-white">
                {CATEGORY_LABELS[category as ExerciseCategory]}
              </h2>
              <Badge variant="primary">{exercises.length} exercices</Badge>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {exercises.map((exercise) => (
                <Card key={exercise.id} hover>
                  <CardHeader>
                    <div className="flex items-start justify-between">
                      <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                        {exercise.name}
                      </h3>
                      <Badge
                        variant={exercise.exercise_type === 'custom' as ExerciseType ? 'secondary' : 'info'}
                        size="sm"
                      >
                        {exercise.exercise_type === 'custom' as ExerciseType ? 'Custom' : 'Externe'}
                      </Badge>
                    </div>
                  </CardHeader>

                  <CardBody>
                    <p className="text-gray-600 dark:text-gray-400 text-sm mb-4">
                      {exercise.description}
                    </p>

                    {exercise.score_unit && (
                      <div className="flex items-center gap-2 mb-4">
                        <span className="text-xs font-medium text-gray-500 dark:text-gray-400">
                          Unit:
                        </span>
                        <Badge variant="primary" size="sm">
                          {exercise.score_unit}
                        </Badge>
                        {exercise.lower_is_better && (
                          <span className="text-xs text-gray-500">
                            (plus bas = meilleur)
                          </span>
                        )}
                      </div>
                    )}

                    <div className="flex gap-2">
                      {exercise.external_url ? (
                        // Diffrencier URLs internes (/games/...) vs externes (https://...)
                        exercise.external_url.startsWith('/') ? (
                          <Link to={exercise.external_url} className="flex-1">
                            <Button variant="outline" size="sm" fullWidth>
                              Lancer l'exercice 
                            </Button>
                          </Link>
                        ) : (
                          <a
                            href={exercise.external_url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="flex-1"
                          >
                            <Button variant="outline" size="sm" fullWidth>
                              Lancer l'exercice 
                            </Button>
                          </a>
                        )
                      ) : exercise.exercise_type === ExerciseTypeEnum.CUSTOM && getGameRoute(exercise) ? (
                        // Custom mini-game available
                        <Button
                          variant="primary"
                          size="sm"
                          fullWidth
                          onClick={() => navigate(getGameRoute(exercise)!)}
                        >
                           Jouer
                        </Button>
                      ) : (
                        <Button variant="outline" size="sm" fullWidth disabled>
                           venir
                        </Button>
                      )}

                      <Link to={`/exercises/${exercise.id}`} className="flex-1">
                        <Button variant="primary" size="sm" fullWidth>
                          Suivre score
                        </Button>
                      </Link>
                    </div>
                  </CardBody>
                </Card>
              ))}
            </div>
          </div>
        ))}

        {/* Empty State */}
        {!isLoading && !error && exercises.length === 0 && (
          <Card>
            <CardBody>
              <div className="text-center py-12">
                <span className="text-6xl mb-4"></span>
                <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">
                  Aucun exercice trouv
                </h3>
                <p className="text-gray-600 dark:text-gray-400">
                  Aucun exercice disponible pour cette catgorie
                </p>
              </div>
            </CardBody>
          </Card>
        )}

        {/* Info Card */}
        <Card>
          <CardHeader
            title=" Comment utiliser les exercices"
            subtitle="Guide rapide pour optimiser ton entranement"
          />
          <CardBody>
            <div className="space-y-3 text-sm text-gray-700 dark:text-gray-300">
              <div className="flex gap-3">
                <span className="text-primary-600 font-bold">1.</span>
                <p>Clique sur "Lancer l'exercice" pour t'entraner sur le site externe</p>
              </div>
              <div className="flex gap-3">
                <span className="text-primary-600 font-bold">2.</span>
                <p>Prends une capture d'cran de ton score (optionnel mais recommand)</p>
              </div>
              <div className="flex gap-3">
                <span className="text-primary-600 font-bold">3.</span>
                <p>Reviens ici et clique "Suivre score" pour enregistrer ta performance</p>
              </div>
              <div className="flex gap-3">
                <span className="text-primary-600 font-bold">4.</span>
                <p>Consulte tes graphiques de progression pour voir ton amlioration !</p>
              </div>
            </div>

            <div className="mt-6 p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
              <p className="text-sm text-blue-900 dark:text-blue-100">
                <strong> Astuce :</strong> Pour de meilleurs rsultats, entrane-toi 15-30 minutes par jour sur 2-3 exercices diffrents. La rgularit est la cl !
              </p>
            </div>
          </CardBody>
        </Card>
      </div>
    </MainLayout>
  )
}
