/**
 * Calendar Page - Training session calendar with booking
 */

import { useState, useEffect, useMemo } from 'react'
import { Calendar, dateFnsLocalizer, Event } from 'react-big-calendar'
import { format, parse, startOfWeek, getDay } from 'date-fns'
import { fr } from 'date-fns/locale'
import MainLayout from '@/components/layout/MainLayout'
import { Card, CardHeader, CardBody, Button, Badge } from '@/components/ui'
import sessionService from '@/services/sessionService'
import { useAuthStore } from '@/store/authStore'
import type { Session, SessionType, SessionStatus } from '@/types/session'
import {
  SESSION_TYPE_LABELS,
  SESSION_TYPE_EMOJIS,
  SESSION_TYPE_COLORS,
  SESSION_STATUS_LABELS,
  SESSION_STATUS_COLORS,
} from '@/types/session'
import SessionModal from '@/components/sessions/SessionModal'
import SessionDetailModal from '@/components/sessions/SessionDetailModal'

import 'react-big-calendar/lib/css/react-big-calendar.css'

// Configure date-fns localizer
const locales = { 'fr': fr }
const localizer = dateFnsLocalizer({
  format,
  parse,
  startOfWeek,
  getDay,
  locales,
})

// Custom event for react-big-calendar
interface CalendarEvent extends Event {
  session: Session
}

export default function CalendarPage() {
  const { user } = useAuthStore()

  const [sessions, setSessions] = useState<Session[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [selectedSession, setSelectedSession] = useState<Session | null>(null)
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false)
  const [isDetailModalOpen, setIsDetailModalOpen] = useState(false)

  // Filters
  const [typeFilter, setTypeFilter] = useState<SessionType | null>(null)
  const [statusFilter, setStatusFilter] = useState<SessionStatus | null>(null)

  useEffect(() => {
    loadSessions()
  }, [typeFilter, statusFilter])

  const loadSessions = async () => {
    setIsLoading(true)
    try {
      const response = await sessionService.getMySessions({
        session_type: typeFilter || undefined,
        status: statusFilter || undefined,
      })
      setSessions(response.sessions)
    } catch (error) {
      console.error('Failed to load sessions:', error)
    } finally {
      setIsLoading(false)
    }
  }

  // Convert sessions to calendar events
  const events: CalendarEvent[] = useMemo(() => {
    return sessions.map((session) => ({
      title: `${SESSION_TYPE_EMOJIS[session.session_type]} ${session.title}`,
      start: new Date(session.start_time),
      end: new Date(session.end_time),
      session,
    }))
  }, [sessions])

  const handleSelectEvent = (event: CalendarEvent) => {
    setSelectedSession(event.session)
    setIsDetailModalOpen(true)
  }

  const handleSelectSlot = ({ start }: { start: Date; end: Date }) => {
    if (!user) return

    // Only coaches and managers can create sessions
    if (user.role === 'COACH' || user.role === 'MANAGER') {
      setSelectedSession({
        id: 0,
        title: '',
        session_type: 'solo' as SessionType,
        status: 'pending' as SessionStatus,
        start_time: start.toISOString(),
        end_time: new Date(start.getTime() + 60 * 60 * 1000).toISOString(), // +1 hour
        created_by_id: user.id,
        participants: [],
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString(),
      })
      setIsCreateModalOpen(true)
    }
  }

  const handleCloseCreateModal = () => {
    setIsCreateModalOpen(false)
    setSelectedSession(null)
    loadSessions()
  }

  const handleCloseDetailModal = () => {
    setIsDetailModalOpen(false)
    setSelectedSession(null)
    loadSessions()
  }

  // Custom event style getter
  const eventStyleGetter = (event: CalendarEvent) => {
    const { session } = event
    let backgroundColor = '#1c3049' // Default primary color
    let borderColor = '#1c3049'

    // Color by status
    switch (session.status) {
      case 'confirmed':
        backgroundColor = '#10b981' // Green
        borderColor = '#10b981'
        break
      case 'cancelled':
        backgroundColor = '#ef4444' // Red
        borderColor = '#ef4444'
        break
      case 'completed':
        backgroundColor = '#6b7280' // Gray
        borderColor = '#6b7280'
        break
      case 'pending':
        backgroundColor = '#f59e0b' // Orange
        borderColor = '#f59e0b'
        break
    }

    return {
      style: {
        backgroundColor,
        borderColor,
        borderLeft: `4px solid ${borderColor}`,
        opacity: session.status === 'cancelled' ? 0.5 : 1,
      },
    }
  }

  if (isLoading) {
    return (
      <MainLayout>
        <div className="text-center py-12">
          <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600" />
          <p className="mt-4 text-gray-600 dark:text-gray-400">Chargement du calendrier...</p>
        </div>
      </MainLayout>
    )
  }

  return (
    <MainLayout>
      <div className="space-y-6">
        {/* Header */}
        <div className="bg-gradient-to-r from-primary-600 to-secondary rounded-xl p-8 text-white">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold mb-2"> Calendrier d'entranement</h1>
              <p className="text-primary-100">
                Planifie et gre tes sessions d'entranement
              </p>
            </div>
            {(user?.role === 'COACH' || user?.role === 'MANAGER') && (
              <Button
                variant="accent"
                size="lg"
                onClick={() => {
                  setSelectedSession({
                    id: 0,
                    title: '',
                    session_type: 'solo' as SessionType,
                    status: 'pending' as SessionStatus,
                    start_time: new Date().toISOString(),
                    end_time: new Date(Date.now() + 60 * 60 * 1000).toISOString(),
                    created_by_id: user.id,
                    participants: [],
                    created_at: new Date().toISOString(),
                    updated_at: new Date().toISOString(),
                  })
                  setIsCreateModalOpen(true)
                }}
              >
                + Nouvelle session
              </Button>
            )}
          </div>
        </div>

        {/* Filters */}
        <Card>
          <CardHeader title="Filtres" />
          <CardBody>
            <div className="flex flex-wrap gap-3">
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">Type de session</p>
                <div className="flex flex-wrap gap-2">
                  <Button
                    variant={typeFilter === null ? 'primary' : 'outline'}
                    size="sm"
                    onClick={() => setTypeFilter(null)}
                  >
                    Tous
                  </Button>
                  {Object.entries(SESSION_TYPE_LABELS).map(([key, label]) => (
                    <Button
                      key={key}
                      variant={typeFilter === key ? 'primary' : 'outline'}
                      size="sm"
                      onClick={() => setTypeFilter(key as SessionType)}
                    >
                      {SESSION_TYPE_EMOJIS[key as SessionType]} {label}
                    </Button>
                  ))}
                </div>
              </div>

              <div className="border-l border-gray-300 dark:border-gray-700 mx-4" />

              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">Statut</p>
                <div className="flex flex-wrap gap-2">
                  <Button
                    variant={statusFilter === null ? 'primary' : 'outline'}
                    size="sm"
                    onClick={() => setStatusFilter(null)}
                  >
                    Tous
                  </Button>
                  {Object.entries(SESSION_STATUS_LABELS).map(([key, label]) => (
                    <Button
                      key={key}
                      variant={statusFilter === key ? 'primary' : 'outline'}
                      size="sm"
                      onClick={() => setStatusFilter(key as SessionStatus)}
                    >
                      {label}
                    </Button>
                  ))}
                </div>
              </div>
            </div>
          </CardBody>
        </Card>

        {/* Calendar */}
        <Card>
          <CardBody>
            <div style={{ height: '700px' }}>
              <Calendar
                localizer={localizer}
                events={events}
                startAccessor="start"
                endAccessor="end"
                culture="fr"
                messages={{
                  next: 'Suivant',
                  previous: 'Prcdent',
                  today: "Aujourd'hui",
                  month: 'Mois',
                  week: 'Semaine',
                  day: 'Jour',
                  agenda: 'Agenda',
                  date: 'Date',
                  time: 'Heure',
                  event: 'Session',
                  noEventsInRange: 'Aucune session dans cette priode',
                  showMore: (total) => `+ ${total} sessions`,
                }}
                onSelectEvent={handleSelectEvent}
                onSelectSlot={handleSelectSlot}
                selectable={user?.role === 'COACH' || user?.role === 'MANAGER'}
                eventPropGetter={eventStyleGetter}
                views={['month', 'week', 'day', 'agenda']}
                defaultView="week"
                step={30}
                timeslots={2}
                min={new Date(2000, 1, 1, 8, 0, 0)} // 8:00 AM
                max={new Date(2000, 1, 1, 23, 0, 0)} // 11:00 PM
              />
            </div>
          </CardBody>
        </Card>

        {/* Info */}
        <Card>
          <CardHeader title=" Comment utiliser le calendrier" />
          <CardBody>
            <div className="space-y-3 text-sm text-gray-700 dark:text-gray-300">
              <div className="flex gap-3">
                <span className="text-primary-600 font-bold">1.</span>
                <p>Clique sur une session pour voir les dtails et t'inscrire</p>
              </div>
              {(user?.role === 'COACH' || user?.role === 'MANAGER') && (
                <>
                  <div className="flex gap-3">
                    <span className="text-primary-600 font-bold">2.</span>
                    <p>Clique sur un crneau vide pour crer une nouvelle session</p>
                  </div>
                  <div className="flex gap-3">
                    <span className="text-primary-600 font-bold">3.</span>
                    <p>Glisse-dpose pour dplacer une session</p>
                  </div>
                </>
              )}
            </div>

            <div className="mt-6 p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
              <p className="text-sm text-blue-900 dark:text-blue-100">
                <strong> Astuce :</strong> Utilise les filtres pour trouver rapidement les sessions
                qui t'intressent !
              </p>
            </div>
          </CardBody>
        </Card>
      </div>

      {/* Modals */}
      {isCreateModalOpen && selectedSession && (
        <SessionModal
          session={selectedSession}
          isOpen={isCreateModalOpen}
          onClose={handleCloseCreateModal}
        />
      )}

      {isDetailModalOpen && selectedSession && (
        <SessionDetailModal
          session={selectedSession}
          isOpen={isDetailModalOpen}
          onClose={handleCloseDetailModal}
        />
      )}
    </MainLayout>
  )
}
