/**
 * Profile Page - User profile management and password change
 */

import { useState, useEffect } from 'react'
import MainLayout from '@/components/layout/MainLayout'
import { Button, Input, AvatarUpload } from '@/components/ui'
import userService from '@/services/userService'
import authService from '@/services/authService'
import notificationService from '@/services/notificationService'
import type { User, UserUpdate, PasswordChange } from '@/types/user'
import type { NotificationPreferences, NotificationPreferencesUpdate } from '@/services/notificationService'
import { UserRole } from '@/types/user'

export default function ProfilePage() {
  const [user, setUser] = useState<User | null>(null)
  const [activeTab, setActiveTab] = useState<'profile' | 'password' | 'notifications'>('profile')
  const [isLoading, setIsLoading] = useState(true)
  const [isSaving, setIsSaving] = useState(false)
  const [message, setMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null)

  // Profile form state
  const [profileData, setProfileData] = useState<UserUpdate>({
    full_name: '',
    bio: '',
    avatar_url: '',
    game_username: '',
    game_uid: '',
    preferred_role: '',
    secondary_role: '',
    skill_level: '',
    discord_username: '',
  })

  // Password form state
  const [passwordData, setPasswordData] = useState<PasswordChange>({
    current_password: '',
    new_password: '',
  })
  const [confirmPassword, setConfirmPassword] = useState('')
  const [showCurrentPassword, setShowCurrentPassword] = useState(false)
  const [showNewPassword, setShowNewPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)

  // Notification preferences state
  const [notificationPreferences, setNotificationPreferences] = useState<NotificationPreferences | null>(null)
  const [isLoadingNotifications, setIsLoadingNotifications] = useState(false)

  // Load user profile
  useEffect(() => {
    loadProfile()
  }, [])

  // Load notification preferences when tab is activated
  useEffect(() => {
    if (activeTab === 'notifications' && !notificationPreferences) {
      loadNotificationPreferences()
    }
  }, [activeTab])

  const loadProfile = async () => {
    try {
      setIsLoading(true)
      const userData = await authService.getCurrentUser()
      setUser(userData)

      // Populate form with current data
      setProfileData({
        full_name: userData.full_name || '',
        bio: userData.bio || '',
        avatar_url: userData.avatar_url || '',
        game_username: userData.game_username || '',
        game_uid: userData.game_uid || '',
        preferred_role: userData.preferred_role || '',
        secondary_role: userData.secondary_role || '',
        skill_level: userData.skill_level || '',
        discord_username: userData.discord_username || '',
      })
    } catch (error: any) {
      setMessage({
        type: 'error',
        text: error.response?.data?.detail || 'Erreur lors du chargement du profil',
      })
    } finally {
      setIsLoading(false)
    }
  }

  const handleProfileUpdate = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSaving(true)
    setMessage(null)

    try {
      const updatedUser = await userService.updateMyProfile(profileData)
      setUser(updatedUser)
      setMessage({ type: 'success', text: 'Profil mis  jour avec succs' })
    } catch (error: any) {
      setMessage({
        type: 'error',
        text: error.response?.data?.detail || 'Erreur lors de la mise  jour',
      })
    } finally {
      setIsSaving(false)
    }
  }

  const handlePasswordChange = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSaving(true)
    setMessage(null)

    // Validate passwords match
    if (passwordData.new_password !== confirmPassword) {
      setMessage({ type: 'error', text: 'Les mots de passe ne correspondent pas' })
      setIsSaving(false)
      return
    }

    try {
      await userService.changePassword(passwordData)
      setMessage({ type: 'success', text: 'Mot de passe modifi avec succs' })

      // Clear password fields
      setPasswordData({ current_password: '', new_password: '' })
      setConfirmPassword('')
    } catch (error: any) {
      setMessage({
        type: 'error',
        text: error.response?.data?.detail || 'Erreur lors du changement de mot de passe',
      })
    } finally {
      setIsSaving(false)
    }
  }

  const loadNotificationPreferences = async () => {
    try {
      setIsLoadingNotifications(true)
      const preferences = await notificationService.getPreferences()
      setNotificationPreferences(preferences)
    } catch (error: any) {
      setMessage({
        type: 'error',
        text: error.response?.data?.detail || 'Erreur lors du chargement des prfrences',
      })
    } finally {
      setIsLoadingNotifications(false)
    }
  }

  const handleNotificationPreferenceToggle = async (key: keyof NotificationPreferencesUpdate) => {
    if (!notificationPreferences) return

    try {
      const updatedValue = !notificationPreferences[key as keyof NotificationPreferences]
      const update: NotificationPreferencesUpdate = { [key]: updatedValue }

      const updated = await notificationService.updatePreferences(update)
      setNotificationPreferences(updated)
      setMessage({ type: 'success', text: 'Prfrences mises  jour' })

      // Clear success message after 2 seconds
      setTimeout(() => setMessage(null), 2000)
    } catch (error: any) {
      setMessage({
        type: 'error',
        text: error.response?.data?.detail || 'Erreur lors de la mise  jour',
      })
    }
  }

  const handleResetNotifications = async () => {
    if (!confirm('Ractiver toutes les notifications ?')) return

    try {
      setIsSaving(true)
      const reset = await notificationService.resetPreferences()
      setNotificationPreferences(reset)
      setMessage({ type: 'success', text: 'Toutes les notifications ont t ractives' })
    } catch (error: any) {
      setMessage({
        type: 'error',
        text: error.response?.data?.detail || 'Erreur lors de la rinitialisation',
      })
    } finally {
      setIsSaving(false)
    }
  }

  const getRoleBadgeColor = (role: UserRole) => {
    switch (role) {
      case UserRole.MANAGER:
        return 'bg-purple-100 text-purple-800 dark:bg-purple-900/30 dark:text-purple-300'
      case UserRole.COACH:
        return 'bg-blue-100 text-blue-800 dark:bg-blue-900/30 dark:text-blue-300'
      default:
        return 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-300'
    }
  }

  const getRoleLabel = (role: UserRole) => {
    switch (role) {
      case UserRole.MANAGER:
        return 'Manager'
      case UserRole.COACH:
        return 'Coach'
      default:
        return 'Joueur'
    }
  }

  if (isLoading) {
    return (
      <MainLayout>
        <div className="flex items-center justify-center min-h-screen">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
        </div>
      </MainLayout>
    )
  }

  if (!user) {
    return (
      <MainLayout>
        <div className="container mx-auto px-4 py-8">
          <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4">
            <p className="text-red-600 dark:text-red-400">Erreur de chargement du profil</p>
          </div>
        </div>
      </MainLayout>
    )
  }

  return (
    <MainLayout>
      <div className="container mx-auto px-4 py-8 max-w-4xl">
      {/* Header */}
      <div className="bg-gradient-to-r from-primary-600 to-primary-700 text-white rounded-xl p-8 mb-8 shadow-lg">
        <div className="flex items-center gap-6">
          {user.avatar_url ? (
            <img
              src={user.avatar_url}
              alt={user.username}
              className="w-20 h-20 rounded-full border-4 border-white/30 object-cover"
            />
          ) : (
            <div className="w-20 h-20 bg-white/20 rounded-full flex items-center justify-center text-4xl font-bold">
              {user.username.charAt(0).toUpperCase()}
            </div>
          )}
          <div className="flex-1">
            <h1 className="text-3xl font-bold mb-2">{user.full_name || user.username}</h1>
            <p className="text-primary-100">@{user.username}  {user.email}</p>
            <div className="mt-2 flex gap-2">
              <span className={`inline-block px-3 py-1 rounded-full text-sm font-medium ${getRoleBadgeColor(user.role)}`}>
                {getRoleLabel(user.role)}
              </span>
              {user.is_super_admin && (
                <span className="inline-block px-3 py-1 rounded-full text-sm font-medium bg-purple-100 text-purple-800 dark:bg-purple-900/30 dark:text-purple-300">
                   Super Admin
                </span>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Message */}
      {message && (
        <div
          className={`mb-6 p-4 rounded-lg ${
            message.type === 'success'
              ? 'bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 text-green-600 dark:text-green-400'
              : 'bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 text-red-600 dark:text-red-400'
          }`}
        >
          {message.text}
        </div>
      )}

      {/* Tabs */}
      <div className="bg-white dark:bg-gray-800 rounded-xl shadow-lg overflow-hidden">
        <div className="flex border-b border-gray-200 dark:border-gray-700">
          <button
            onClick={() => setActiveTab('profile')}
            className={`flex-1 px-6 py-4 font-medium transition-colors ${
              activeTab === 'profile'
                ? 'bg-primary-50 dark:bg-primary-900/20 text-primary-600 dark:text-primary-400 border-b-2 border-primary-600'
                : 'text-gray-600 dark:text-gray-400 hover:bg-gray-50 dark:hover:bg-gray-700/50'
            }`}
          >
             Mon profil
          </button>
          <button
            onClick={() => setActiveTab('password')}
            className={`flex-1 px-6 py-4 font-medium transition-colors ${
              activeTab === 'password'
                ? 'bg-primary-50 dark:bg-primary-900/20 text-primary-600 dark:text-primary-400 border-b-2 border-primary-600'
                : 'text-gray-600 dark:text-gray-400 hover:bg-gray-50 dark:hover:bg-gray-700/50'
            }`}
          >
             Mot de passe
          </button>
          <button
            onClick={() => setActiveTab('notifications')}
            className={`flex-1 px-6 py-4 font-medium transition-colors ${
              activeTab === 'notifications'
                ? 'bg-primary-50 dark:bg-primary-900/20 text-primary-600 dark:text-primary-400 border-b-2 border-primary-600'
                : 'text-gray-600 dark:text-gray-400 hover:bg-gray-50 dark:hover:bg-gray-700/50'
            }`}
          >
             Notifications
          </button>
        </div>

        {/* Profile Tab */}
        {activeTab === 'profile' && (
          <form onSubmit={handleProfileUpdate} className="p-6 space-y-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
              Informations personnelles
            </h3>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <Input
                label="Nom complet"
                type="text"
                value={profileData.full_name || ''}
                onChange={(e) => setProfileData({ ...profileData, full_name: e.target.value })}
                placeholder="Prnom Nom"
              />

              <Input
                label="Pseudo Discord"
                type="text"
                value={profileData.discord_username || ''}
                onChange={(e) => setProfileData({ ...profileData, discord_username: e.target.value })}
                placeholder="VotrePseudo#1234"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                Bio
              </label>
              <textarea
                value={profileData.bio || ''}
                onChange={(e) => setProfileData({ ...profileData, bio: e.target.value })}
                placeholder="Parlez-nous de vous..."
                rows={4}
                className="w-full px-3 py-2 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent dark:bg-gray-900 dark:text-white"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                Photo de profil
              </label>
              <AvatarUpload
                currentAvatar={profileData.avatar_url}
                onUploadSuccess={(avatarUrl) => {
                  setProfileData({ ...profileData, avatar_url: avatarUrl })
                  if (user) {
                    setUser({ ...user, avatar_url: avatarUrl })
                  }
                }}
                onUploadError={(error) => {
                  setMessage({ type: 'error', text: error })
                }}
              />
            </div>

            {/* Gaming profile (for players, coaches, and managers) */}
            {(user.role === UserRole.JOUEUR || user.role === UserRole.COACH || user.role === UserRole.MANAGER) && (
              <>
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white border-t border-gray-200 dark:border-gray-700 pt-6">
                   Profil Gaming
                </h3>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <Input
                    label="Pseudo HOK (Honor of Kings)"
                    type="text"
                    value={profileData.game_username || ''}
                    onChange={(e) => setProfileData({ ...profileData, game_username: e.target.value })}
                    placeholder="VotrePseudoHOK"
                  />

                  <Input
                    label="UID HOK (Player ID)"
                    type="text"
                    value={profileData.game_uid || ''}
                    onChange={(e) => setProfileData({ ...profileData, game_uid: e.target.value })}
                    placeholder="123456789"
                  />
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Rle en jeu prfr
                    </label>
                    <select
                      value={profileData.preferred_role || ''}
                      onChange={(e) => setProfileData({ ...profileData, preferred_role: e.target.value })}
                      className="w-full px-3 py-2 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent dark:bg-gray-900 dark:text-white"
                    >
                      <option value="">Slectionner...</option>
                      <option value="Roam/Support">Roam/Support</option>
                      <option value="ADC">ADC</option>
                      <option value="Jungle">Jungle</option>
                      <option value="Clash Lane">Clash Lane</option>
                      <option value="Mid Lane">Mid Lane</option>
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Rle secondaire
                    </label>
                    <select
                      value={profileData.secondary_role || ''}
                      onChange={(e) => setProfileData({ ...profileData, secondary_role: e.target.value })}
                      className="w-full px-3 py-2 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent dark:bg-gray-900 dark:text-white"
                    >
                      <option value="">Slectionner...</option>
                      <option value="Roam/Support">Roam/Support</option>
                      <option value="ADC">ADC</option>
                      <option value="Jungle">Jungle</option>
                      <option value="Clash Lane">Clash Lane</option>
                      <option value="Mid Lane">Mid Lane</option>
                    </select>
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Niveau de comptence
                    </label>
                    <select
                      value={profileData.skill_level || ''}
                      onChange={(e) => setProfileData({ ...profileData, skill_level: e.target.value })}
                      className="w-full px-3 py-2 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent dark:bg-gray-900 dark:text-white"
                    >
                      <option value="">Slectionner...</option>
                      <option value="Dbutant">Dbutant</option>
                      <option value="Intermdiaire">Intermdiaire</option>
                      <option value="Avanc">Avanc</option>
                      <option value="Expert">Expert</option>
                    </select>
                  </div>
                </div>
              </>
            )}

            {/* System Info */}
            <div className="border-t border-gray-200 dark:border-gray-700 pt-6">
              <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                Informations systme
              </h3>
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <span className="text-gray-600 dark:text-gray-400">Rle plateforme:</span>
                  <div className="mt-1">
                    <span className={`inline-block px-3 py-1 rounded-full text-sm font-medium ${getRoleBadgeColor(user.role)}`}>
                      {getRoleLabel(user.role)}
                    </span>
                  </div>
                </div>
                <div>
                  <span className="text-gray-600 dark:text-gray-400">Membre depuis:</span>
                  <div className="mt-1 text-gray-900 dark:text-white font-medium">
                    {new Date(user.created_at).toLocaleDateString('fr-FR')}
                  </div>
                </div>
              </div>
            </div>

            <div className="flex justify-end pt-4 border-t border-gray-200 dark:border-gray-700">
              <Button type="submit" variant="primary" isLoading={isSaving}>
                 Enregistrer les modifications
              </Button>
            </div>
          </form>
        )}

        {/* Password Tab */}
        {activeTab === 'password' && (
          <form onSubmit={handlePasswordChange} className="p-6 space-y-6">
            <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg p-4 mb-6">
              <p className="text-sm text-blue-600 dark:text-blue-400">
                <strong>Exigences du mot de passe :</strong>
                <br />
                 Au moins 8 caractres
                <br />
                 Au moins 1 chiffre
                <br /> Au moins 1 lettre majuscule
              </p>
            </div>

            <div className="relative">
              <Input
                label="Mot de passe actuel"
                type={showCurrentPassword ? "text" : "password"}
                value={passwordData.current_password}
                onChange={(e) => setPasswordData({ ...passwordData, current_password: e.target.value })}
                placeholder="Votre mot de passe actuel"
                required
              />
              <button
                type="button"
                onClick={() => setShowCurrentPassword(!showCurrentPassword)}
                className="absolute right-3 top-9 text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200"
              >
                {showCurrentPassword ? (
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.88 9.88l-3.29-3.29m7.532 7.532l3.29 3.29M3 3l3.59 3.59m0 0A9.953 9.953 0 0112 5c4.478 0 8.268 2.943 9.543 7a10.025 10.025 0 01-4.132 5.411m0 0L21 21" />
                  </svg>
                ) : (
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                  </svg>
                )}
              </button>
            </div>

            <div className="relative">
              <Input
                label="Nouveau mot de passe"
                type={showNewPassword ? "text" : "password"}
                value={passwordData.new_password}
                onChange={(e) => setPasswordData({ ...passwordData, new_password: e.target.value })}
                placeholder="Votre nouveau mot de passe"
                required
              />
              <button
                type="button"
                onClick={() => setShowNewPassword(!showNewPassword)}
                className="absolute right-3 top-9 text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200"
              >
                {showNewPassword ? (
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.88 9.88l-3.29-3.29m7.532 7.532l3.29 3.29M3 3l3.59 3.59m0 0A9.953 9.953 0 0112 5c4.478 0 8.268 2.943 9.543 7a10.025 10.025 0 01-4.132 5.411m0 0L21 21" />
                  </svg>
                ) : (
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                  </svg>
                )}
              </button>
            </div>

            <div className="relative">
              <Input
                label="Confirmer le nouveau mot de passe"
                type={showConfirmPassword ? "text" : "password"}
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                placeholder="Confirmer le nouveau mot de passe"
                required
              />
              <button
                type="button"
                onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                className="absolute right-3 top-9 text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200"
              >
                {showConfirmPassword ? (
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.88 9.88l-3.29-3.29m7.532 7.532l3.29 3.29M3 3l3.59 3.59m0 0A9.953 9.953 0 0112 5c4.478 0 8.268 2.943 9.543 7a10.025 10.025 0 01-4.132 5.411m0 0L21 21" />
                  </svg>
                ) : (
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                  </svg>
                )}
              </button>
            </div>

            <div className="flex justify-end pt-4 border-t border-gray-200 dark:border-gray-700">
              <Button type="submit" variant="primary" isLoading={isSaving}>
                 Changer le mot de passe
              </Button>
            </div>
          </form>
        )}

        {/* Notifications Tab */}
        {activeTab === 'notifications' && (
          <div className="p-6 space-y-6">
            <div className="flex items-center justify-between mb-6">
              <div>
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                  Prfrences de notification par email
                </h3>
                <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
                  Choisissez les notifications que vous souhaitez recevoir par email
                </p>
              </div>
              <Button
                variant="secondary"
                onClick={handleResetNotifications}
                isLoading={isSaving}
              >
                 Tout ractiver
              </Button>
            </div>

            {isLoadingNotifications ? (
              <div className="flex items-center justify-center py-12">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600"></div>
              </div>
            ) : notificationPreferences ? (
              <div className="space-y-4">
                {/* Session Notifications */}
                <div className="border-b border-gray-200 dark:border-gray-700 pb-4">
                  <h4 className="text-md font-medium text-gray-900 dark:text-white mb-3">
                     Sessions d'entranement
                  </h4>
                  <div className="space-y-3">
                    <NotificationToggle
                      label="Nouvelle session cre"
                      description="Recevoir un email quand une nouvelle session est cre"
                      checked={notificationPreferences.session_created}
                      onChange={() => handleNotificationPreferenceToggle('session_created')}
                    />
                    <NotificationToggle
                      label="Invitation  une session"
                      description="Recevoir un email quand vous tes invit  une session"
                      checked={notificationPreferences.session_invitation}
                      onChange={() => handleNotificationPreferenceToggle('session_invitation')}
                    />
                    <NotificationToggle
                      label="Rappel de session"
                      description="Recevoir un rappel avant le dbut d'une session"
                      checked={notificationPreferences.session_reminder}
                      onChange={() => handleNotificationPreferenceToggle('session_reminder')}
                    />
                  </div>
                </div>

                {/* Exercise Notifications */}
                <div className="border-b border-gray-200 dark:border-gray-700 pb-4">
                  <h4 className="text-md font-medium text-gray-900 dark:text-white mb-3">
                     Exercices
                  </h4>
                  <div className="space-y-3">
                    <NotificationToggle
                      label="Nouvel exercice assign"
                      description="Recevoir un email quand un exercice vous est assign"
                      checked={notificationPreferences.exercise_assigned}
                      onChange={() => handleNotificationPreferenceToggle('exercise_assigned')}
                    />
                    <NotificationToggle
                      label="Performance enregistre"
                      description="Recevoir un email quand votre performance est enregistre"
                      checked={notificationPreferences.performance_recorded}
                      onChange={() => handleNotificationPreferenceToggle('performance_recorded')}
                    />
                  </div>
                </div>

                {/* Communication Notifications */}
                <div className="border-b border-gray-200 dark:border-gray-700 pb-4">
                  <h4 className="text-md font-medium text-gray-900 dark:text-white mb-3">
                     Communication
                  </h4>
                  <div className="space-y-3">
                    <NotificationToggle
                      label="Message du coach"
                      description="Recevoir un email quand le coach vous envoie un message"
                      checked={notificationPreferences.coach_message}
                      onChange={() => handleNotificationPreferenceToggle('coach_message')}
                    />
                  </div>
                </div>

                {/* System Notifications */}
                <div>
                  <h4 className="text-md font-medium text-gray-900 dark:text-white mb-3">
                     Systme
                  </h4>
                  <div className="space-y-3">
                    <NotificationToggle
                      label="Mises  jour du compte"
                      description="Recevoir les notifications importantes concernant votre compte"
                      checked={notificationPreferences.account_updates}
                      onChange={() => handleNotificationPreferenceToggle('account_updates')}
                    />
                  </div>
                </div>
              </div>
            ) : (
              <div className="bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-800 rounded-lg p-4">
                <p className="text-yellow-600 dark:text-yellow-400">
                  Impossible de charger les prfrences de notification
                </p>
              </div>
            )}
          </div>
        )}
      </div>
      </div>
    </MainLayout>
  )
}

// Toggle component for notification preferences
function NotificationToggle({
  label,
  description,
  checked,
  onChange,
}: {
  label: string
  description: string
  checked: boolean
  onChange: () => void
}) {
  return (
    <div className="flex items-center justify-between p-4 bg-gray-50 dark:bg-gray-900/50 rounded-lg">
      <div className="flex-1">
        <p className="font-medium text-gray-900 dark:text-white">{label}</p>
        <p className="text-sm text-gray-600 dark:text-gray-400">{description}</p>
      </div>
      <button
        type="button"
        onClick={onChange}
        className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2 ${
          checked ? 'bg-primary-600' : 'bg-gray-300 dark:bg-gray-600'
        }`}
      >
        <span
          className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${
            checked ? 'translate-x-6' : 'translate-x-1'
          }`}
        />
      </button>
    </div>
  )
}
