/**
 * Session Modal - Create/Edit session
 */

import { useState, useEffect } from 'react'
import { Button, Input } from '@/components/ui'
import sessionService from '@/services/sessionService'
import type { Session, SessionCreate, SessionType } from '@/types/session'
import { SESSION_TYPE_LABELS, SESSION_TYPE_EMOJIS } from '@/types/session'

interface SessionModalProps {
  session: Session
  isOpen: boolean
  onClose: () => void
}

export default function SessionModal({ session, isOpen, onClose }: SessionModalProps) {
  const [title, setTitle] = useState(session.title || '')
  const [description, setDescription] = useState(session.description || '')
  const [sessionType, setSessionType] = useState<SessionType>(session.session_type)
  const [startTime, setStartTime] = useState('')
  const [endTime, setEndTime] = useState('')
  const [meetingUrl, setMeetingUrl] = useState(session.meeting_url || '')
  const [notes, setNotes] = useState(session.notes || '')
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [error, setError] = useState('')

  const isEdit = session.id > 0

  useEffect(() => {
    if (session.start_time) {
      const start = new Date(session.start_time)
      setStartTime(start.toISOString().slice(0, 16))
    }
    if (session.end_time) {
      const end = new Date(session.end_time)
      setEndTime(end.toISOString().slice(0, 16))
    }
  }, [session])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)
    setError('')

    try {
      const sessionData: SessionCreate = {
        title,
        description: description || undefined,
        session_type: sessionType,
        start_time: new Date(startTime).toISOString(),
        end_time: new Date(endTime).toISOString(),
        meeting_url: meetingUrl || undefined,
        notes: notes || undefined,
      }

      if (isEdit) {
        await sessionService.updateSession(session.id, sessionData)
      } else {
        await sessionService.createSession(sessionData)
      }

      onClose()
    } catch (err: any) {
      setError(err.response?.data?.detail || 'Erreur lors de la sauvegarde')
    } finally {
      setIsSubmitting(false)
    }
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
      <div className="bg-white dark:bg-gray-800 rounded-xl shadow-2xl max-w-2xl w-full mx-4 max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="bg-primary-600 text-white p-6 rounded-t-xl">
          <h2 className="text-2xl font-bold">
            {isEdit ? ' Modifier la session' : '+ Nouvelle session'}
          </h2>
        </div>

        {/* Body */}
        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          {error && (
            <div className="p-3 bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg">
              <p className="text-sm text-red-600 dark:text-red-400">{error}</p>
            </div>
          )}

          <Input
            label="Titre de la session"
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder="Ex: Entranement macro gameplay"
            required
          />

          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Type de session
            </label>
            <div className="grid grid-cols-2 sm:grid-cols-3 gap-2">
              {Object.entries(SESSION_TYPE_LABELS).map(([key, label]) => (
                <button
                  key={key}
                  type="button"
                  onClick={() => setSessionType(key as SessionType)}
                  className={`p-3 rounded-lg border-2 transition-all ${
                    sessionType === key
                      ? 'border-primary-600 bg-primary-50 dark:bg-primary-900/20 text-primary-900 dark:text-primary-100'
                      : 'border-gray-300 dark:border-gray-700 hover:border-primary-400 text-gray-900 dark:text-gray-100 bg-white dark:bg-gray-800'
                  }`}
                >
                  <span className="text-2xl mb-1 block">{SESSION_TYPE_EMOJIS[key as SessionType]}</span>
                  <span className="text-sm font-medium">{label}</span>
                </button>
              ))}
            </div>
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
            <Input
              label="Dbut"
              type="datetime-local"
              value={startTime}
              onChange={(e) => setStartTime(e.target.value)}
              required
            />

            <Input
              label="Fin"
              type="datetime-local"
              value={endTime}
              onChange={(e) => setEndTime(e.target.value)}
              required
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Description
            </label>
            <textarea
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="Objectifs de la session, focus..."
              rows={3}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent dark:bg-gray-900 dark:text-white"
            />
          </div>

          <Input
            label="Lien de runion (optionnel)"
            type="url"
            value={meetingUrl}
            onChange={(e) => setMeetingUrl(e.target.value)}
            placeholder="https://discord.gg/..."
            helperText="Discord, Zoom, etc."
          />

          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Notes coach (optionnel)
            </label>
            <textarea
              value={notes}
              onChange={(e) => setNotes(e.target.value)}
              placeholder="Notes internes, prparation..."
              rows={2}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent dark:bg-gray-900 dark:text-white"
            />
          </div>

          {/* Footer */}
          <div className="flex gap-3 pt-4 border-t border-gray-200 dark:border-gray-700">
            <Button
              type="button"
              variant="outline"
              onClick={onClose}
              fullWidth
            >
              Annuler
            </Button>
            <Button
              type="submit"
              variant="primary"
              isLoading={isSubmitting}
              fullWidth
            >
              {isEdit ? 'Enregistrer' : 'Crer la session'}
            </Button>
          </div>
        </form>
      </div>
    </div>
  )
}
