/**
 * Notification Service - Manage user notification preferences
 */

import api from './api'

export interface NotificationPreferences {
  id: number
  user_id: number
  session_created: boolean
  session_invitation: boolean
  session_reminder: boolean
  exercise_assigned: boolean
  performance_recorded: boolean
  coach_message: boolean
  account_updates: boolean
}

export interface NotificationPreferencesUpdate {
  session_created?: boolean
  session_invitation?: boolean
  session_reminder?: boolean
  exercise_assigned?: boolean
  performance_recorded?: boolean
  coach_message?: boolean
  account_updates?: boolean
}

const notificationService = {
  /**
   * Get current user's notification preferences
   */
  async getPreferences(): Promise<NotificationPreferences> {
    const response = await api.get('/notifications/preferences')
    return response.data
  },

  /**
   * Update notification preferences
   */
  async updatePreferences(
    preferences: NotificationPreferencesUpdate
  ): Promise<NotificationPreferences> {
    const response = await api.put('/notifications/preferences', preferences)
    return response.data
  },

  /**
   * Reset all notification preferences to defaults (all enabled)
   */
  async resetPreferences(): Promise<NotificationPreferences> {
    const response = await api.post('/notifications/preferences/reset')
    return response.data
  },
}

export default notificationService
